window.jQuery = function(selectorOrArray){
    let elements
    if(typeof selectorOrArray === 'string' ){
        elements = document.querySelectorAll(selectorOrArray)
    }else if(selectorOrArray instanceof Array){
        elements = selectorOrArray
    }
    return {
        oldApi:selectorOrArray.oldApi,  //ËøîÂõûÊï¥‰∏™ÂØπË±°ÁöÑoldApi
        addClass(className){
            for(let i=0;i<elements.length;i++){
                elements[i].classList.add(className)
            }
            return this
        },
        find(selector){
            let classArray=[]
            for(let i = 0;i<elements.length;i++){
                const element2 = Array.from(elements[i].querySelectorAll(selector))
                classArray=classArray.concat(element2)
            }
            classArray.oldApi = this   //Ëøô‰∏™ÊòØÊóßÁöÑapi
            return jQuery(classArray) //ÂæóÂà∞‰∏Ä‰∏™Êñ∞ÁöÑapiÂØπË±°Êù•Êìç‰Ωúapi
        },
        end(){
            return this.oldApi   //ËøôÈáåÁöÑendÊòØÊñ∞ÁöÑapi
        },
        each(fun){
            for(let i= 0;i<elements.length;i++){
                fun.call(null,elements[i])
            }
            return this
        },
        parent(){ //ÂèØËÉΩ‰ºöÂá∫Áé∞Â§ö‰∏™arrayÔºåÊ†πÊçÆÊÉÖÂÜµËøõË°åÂà§Êñ≠
            let parentArray=[]
            this.each((node)=>{
                 if(parentArray.indexOf(node.parentNode)===-1){ //ÂéªÈáçÔºåÈò≤Ê≠¢ÊâìÂá∫Â§ö‰∏™Áõ∏ÂêåÁöÑ
                    parentArray.push(node.parentNode)
                 }
            })
            return jQuery(parentArray)
        },
        children(){
            let chidrenArray=[]
            this.each((node)=>{
                chidrenArray.push(...node.children)//ÂèØËÉΩÊúâÂ§ö‰∏™Â≠êarray
            })
            return jQuery(chidrenArray)
        },
        siblings(){
            let siblingArray = []
            this.each((node)=>{
                    siblingArray.push(...Array.from(node.parentNode.children))
                    siblingArray = siblingArray.filter(item=>item!=node)  //ËøîÂõûÁ¨¶ÂêàÊù°‰ª∂ÁöÑÊâÄÊúâÂÖÉÁ¥†
                })
                return jQuery(siblingArray);
            }
        }
}
window.$ = window.jQuery